.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
  flex-direction: row;
  width: 95%;
  margin: 1%;
  border-bottom: 1px solid rgb(174, 174, 174);
}

.header-left {
  width: 50%;
  display: flex;
  flex: 1;
  align-items: center;
}

.title-box {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-end;
}

.Lama {
  height: 8%;
  width: 12%;
  border-radius: 20px;
}
.name {
  flex: 1;
  padding: 8px 12px;
  text-decoration: none;
  color: #000000;
  font-size: 35px;
  font-weight: bold;
}

.future {
  flex: 1;
  padding: 8px 12px;
  text-decoration: none;
  color: #000000;
  font-weight: bold;
}
.header-right {
  width: 50%;
  text-align: right;
}

.link {
  margin: 1%;
  padding: 8px 12px;
  user-select: none;
  color: #212529;
  font-size: 20px;
  text-decoration: none;
}

.link:hover {
  background-color: antiquewhite;
  border-radius: 4px;
}

@media screen and (max-width: 768px) {
  .header-container {
    display: flex;
    flex: 1;
    text-align: center;
    flex-direction: column;
    padding: 5%;
    border-bottom: 1px solid #ced4da;
  }
  .header-left {
    width: 80%;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
  }

  .header-right {
    width: 70%;
    padding: 10px;
    text-align: center;
  }
  .Lama {
    width: 10%;
  }
  .name {
    margin-bottom: 1%;
    font-size: 20px;
    font-weight: 500;
  }

  .future {
    font-size: 12px;
  }

  .link {
    font-size: 14px;
  }
}
