.About-box {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.About-body1 {
  text-align: center;
  flex: 1;
  height: 50%;
  display: flex;
  margin-top: 5%;
}

.About-body2 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.About-photo {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin: 5%;
}

.rio {
  height: 450px;
  width: 600px;
  border-radius: 50px;
}

.About-intro {
  display: flex;
  flex-direction: column;
  flex: 1;
  list-style: none;
  justify-content: center;
}

.intro-detail {
  text-align: start;
  padding: 8px 12px;
}

.middle-title {
  font-size: 32px;
  margin-top: 50px;
}
.second-title {
  margin-top: 50px;
  font-size: 28px;
}
.element {
  margin-top: 20px;
  font-size: 20px;
}

.experience-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.experience-detail {
  justify-content: center;
  align-items: center;
  width: 50%;
  font-size: 16px;
  margin-top: 10px;
  list-style: none;
  display: flex;
}

.middle-title {
  text-align: center;
}

.body2-box {
  display: flex;
  text-align: center;
  flex-direction: column;
  width: 100%;
  margin: 5%;
}

.About-stack {
  flex: 1;
  margin-bottom: 5%;
}
.About-education {
  flex: 1;
  margin-bottom: 5%;
}
.About-experience {
  flex: 1;
  margin-bottom: 5%;
}
.About-contact {
  flex: 1;
  margin-bottom: 5%;
}

.email-box {
  display: flex;
  justify-content: center;
  padding: 8px 12px;
  margin-top: 20px;
}

.email-icon {
  padding: 8px 12px;
}

.contact-element {
  padding: 8px 12px;
  font-size: 20px;
}

.icon-box {
  display: flex;
  flex: 1;
  justify-content: center;
  padding: 8px 12px;
}

.github-icon2 {
  text-decoration: none;
  color: black;
  padding: 8px 12px;
}

.medium-icon {
  text-decoration: none;
  padding: 8px 12px;
  color: black;
}


@media screen and (max-width: 1024px) {
  .About-body1 {
    flex-direction: column;
    align-items: center;
  }

  .middle-title {
    font-size: 20px;
    margin-top: 50px;
  }
  .second-title {
    margin-top: 50px;
    font-size: 16px;
  }
  .element {
    margin-top: 20px;
    font-size: 14px;
  }

  .experience-detail {
    font-size: 12px;
    margin-top: 10px;
  }

  .contact-element {
    align-items: center;
    font-size: 12px;
  }

  .About-intro {
    font-size: 12px;
  }

  .intro-detail {
    padding: 4px 8px;
  }
  .email-box {
    display: flex;
    align-items: center;
  }
  .rio {
    height: 210px;
    width: 280px;
  }
}
