.projectDesc-container {
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 5%;
}

.projectDesc-title {
  display: flex;
  flex-direction: column;
  margin-bottom: 3%;
}

.notionEmpty {
  flex: 1;
  display: none;
}

.linkServiceEmpty {
  flex: 1;
  display: none;
}

.title-name {
  font-size: 24px;
  margin-bottom: 1%;
}

.role-position {
  margin-bottom: 1%;
}
.title-role {
  font-size: 18px;
  padding-left: 10px;
}

.title-position {
  font-size: 18px;
}

.title-duration {
  font-size: 16px;
  margin-bottom: 1%;
}

.title-emoticon {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.github {
  font-size: 14px;
  flex: 1;
  margin-bottom: 1%;
}
.github-icon {
  padding-right: 5px;
}
.github-text {
}

.notion {
  flex: 1;
  font-size: 14px;
}
.notion-icon {
  padding-right: 5px;
}

.linkService {
  font-size: 14px;
  flex: 1;
  margin-bottom: 1%;
}

.description-title {
  font-size: 20px;
  margin-bottom: 1%;
}

.projectDesc-description {
  display: flex;
  flex-direction: column;
  margin-bottom: 3%;
}

.description-detail {
  font-size: 16px;
}

.projectDesc-stack {
  display: flex;
  flex-direction: column;
  margin-bottom: 3%;
}
.stack-title {
  font-size: 20px;
  margin-bottom: 1%;
}
.stack-detail {
  font-size: 16px;
}

.projectDesc-contribution {
  display: flex;
  flex-direction: column;
  margin-bottom: 3%;
}

.contribution-title {
  font-size: 20px;
  margin-bottom: 1%;
}

.contribution-detail {
  font-size: 16px;
  padding-bottom: 1%;
  list-style: none;
}

@media screen and (max-width: 768px) {
  .projectDesc-container {
    display: flex;
    flex: 1;
    flex-direction: column;
    width: 100%;
  }
  .projectDesc-title {
    width: 100%;
  }

  .title-name {
    text-align: center;
    font-size: 20px;
    width: 100%;
    margin-bottom: 5%;
  }

  .role-position {
    margin-bottom: 1%;
  }
  .title-role {
    font-size: 14px;
    padding-left: 10px;
  }

  .title-position {
    font-size: 14px;
  }

  .title-duration {
    font-size: 12px;
    margin-bottom: 1%;
  }

  .title-emoticon {
    display: flex;
    flex: 1;
  }

  .github {
    font-size: 10px;
    flex: 1;
    margin-bottom: 1%;
  }
  .notion {
    flex: 1;
    font-size: 10px;
  }
  .linkService {
    flex: 1;
    font-size: 10px;
  }

  .description-title {
    font-size: 16px;
    margin-bottom: 1%;
  }

  .projectDesc-description {
    display: flex;
    flex-direction: column;
    margin-bottom: 3%;
  }

  .description-detail {
    font-size: 12px;
  }

  .projectDesc-stack {
    display: flex;
    flex-direction: column;
    margin-bottom: 3%;
  }
  .stack-title {
    font-size: 16px;
    margin-bottom: 1%;
  }
  .stack-detail {
    font-size: 12px;
  }

  .projectDesc-contribution {
    display: flex;
    flex-direction: column;
    margin-bottom: 3%;
  }

  .contribution-title {
    font-size: 16px;
    margin-bottom: 1%;
  }

  .contribution-detail {
    font-size: 12px;
    padding-bottom: 3%;
  }
}
